<template>
  <div :class="['home', isBlurring ? 'blurring' : '']">
    <div v-for="editor in editors"
      :key="editor.uuid"
      class="editor" :class="[activeEditorUUID === editor.uuid ? 'active' : '', theme]"
      @click="setActiveEditor(editor.uuid)">
      <div class="actions">
        <span class="button" v-if="editors.length > 1" @click="removeEditor(editor.uuid)">×</span>
      </div>
      <textarea
        :id="`t-${editor.uuid}`"
        :placeholder="placeholder"
        v-model="editor.note"
        @input="onInput"
        @keydown.tab.stop.prevent="onTab"
        @keyup.tab.stop
        @keyup.esc="unfocus(editor.uuid)"
        @blur="unfocus(editor.uuid)"></textarea>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import Events from '@/events'
import uuid from '@/uuid'
import draft from '@/draft'
import lorem from '@/helpers/lorem'

export default {
  name: 'home',

  data () {
    return {
      placeholder: 'Write something...',
      isBlurring: false,
      editors: [],
      activeEditorUUID: 0
    }
  },

  computed: {
    ...mapGetters([
      'theme'
    ])
  },

  methods: {
    unfocus (_uuid) {
      this.$el.querySelector(`#t-${_uuid}`).blur()
      this.setActiveEditor(0)
    },

    setActiveEditor (_uuid) {
      this.activeEditorUUID = _uuid
    },

    newEditor () {
      this.editors.push({ uuid: uuid() })
    },

    removeEditor (_uuid) {
      this.editors = this.editors.filter((editor) => {
        return editor.uuid !== _uuid
      })
      draft.removeDraft(_uuid)
    },

    saveDrafts () {
      const allDrafts = this.editors.map((editor) => {
        return { uuid: editor.uuid, note: editor.note }
      })
      draft.saveAllDrafts(allDrafts)
    },

    onInput () {
      this.$nextTick(() => {
        this.saveDrafts()
      })
    },

    // This function is currently only for demo purpose
    onTab (evt) {
      const textarea = evt.target
      const checkLength = 'lorem.en'.length
      const checkStart = textarea.selectionStart - checkLength
      const checkString = textarea.value.slice(checkStart, checkStart + checkLength)
      const match = checkString.match(/lorem\.(\w\w)/)
      const locale = match && match[1]
      // The locale could be en, ch, jp, etc.
      if (locale) {
        // Remove `lorem.xx` first
        textarea.value = textarea.value.replace(match[0], '')
        lorem.insert(textarea, locale)
        // Trigger a DOM 'input' event so that the value can sync back to Vue data
        const event = document.createEvent('HTMLEvents')
        event.initEvent('input', false, true)
        textarea.dispatchEvent(event)
      }
    }
  },

  mounted () {
    Events.$on('blurring', (state) => {
      this.isBlurring = state
    })
    Events.$on('vertical-split-window', () => {
      this.newEditor()
    })
    Events.$on('save-drafts', this.saveDrafts)

    // Load drafts from previous sessions
    const allDrafts = draft.loadAllDrafts()
    if (allDrafts.length > 0) {
      allDrafts.map((draft) => {
        this.editors.push({ uuid: draft.uuid, note: draft.note })
      })
    } else {
      this.editors.push({ uuid: uuid(), note: '' })
    }
  }
}
</script>

<style lang="stylus">
@import '~@/theme.styl'

.home
  &.blurring
    filter: blur(5px)
  padding-top 50px
  display flex
  flex 1
  justify-content center

  .editor
    display flex
    flex 1
    width 100%
    max-width 640px
    position relative
    padding 1em
    border-top 2px solid transparent

    &:not(:first-child)
      border-left 2px solid $theme-default-border
      &.theme-dark
        border-left 2px solid $theme-dark-border

    &.active:not(:only-child)
      border-top 2px solid $theme-default-highlight

    .actions
      opacity 0
      position absolute
      top 0
      right 5px
      font-size 1.2em
      .button
        cursor pointer
        font-size 1.5em
        line-height 1em
        color #aaa
    &.active .actions
      opacity 1

    textarea
      font-family 'Open Sans Condensed'
      width 100%
      border none
      overflow auto
      outline none
      -webkit-box-shadow none
      -moz-box-shadow none
      color $theme-default-fg
      box-shadow none
      background-color transparent
      font-size 1em
      resize none

      &:not(:focus)
        color $theme-default-fg-weak
    &.theme-dark textarea
      color $theme-dark-fg
      &:not(:focus)
        color $theme-dark-fg-weak
</style>



