<template>
  <div class="modal" v-if="show" @click="clickHandler">
    <div class="card">
      <div class="content">
        <h3>About</h3>
        <p><span class="brand">z0ey</span><sup>&reg;</sup> is a minimal word processing app aims to provide a distraction-free environment for crafting notes, prose, and poems.</p>

        <h3>Credits</h3>
        <p><span class="brand">z0ey</span><sup>&reg;</sup> is made with ❤️ by @kfei</p>
      </div>
    </div>
  </div>
</template>

<script>
import Events from '@/events'

export default {
  name: 'modal',

  data () {
    return {
      show: false
    }
  },

  methods: {
    openModal () {
      Events.$emit('blurring', true)
      this.show = true
    },

    closeModal () {
      this.show = false
      Events.$emit('blurring', false)
    },

    clickHandler (evt) {
      if (!evt.target.closest('.content')) {
        this.closeModal()
      }
    }
  },

  mounted () {
    Events.$on('logoClick', this.openModal)
    Events.$on('keyup-esc', this.closeModal)
  }
}
</script>

<style lang="stylus">
@import '~@/theme.styl'

.modal
  font-family 'Open Sans Condensed'
  font-size 1.2em
  color $theme-default-fg
  position absolute
  left 0
  top 0
  height 100%
  width 100%
  display flex
  align-items center
  justify-content center
  background-color rgba(255, 255, 255, 0.6)

  h1, h2, h3, h4, h5
    font-family 'Sacramento'

  .card
    display flex
    align-items center
    justify-content center
    min-width 320px
    max-width 320px
    min-height 250px
    background-color white
    box-shadow 0 0.5px 0 0 #ffffff inset, 0 1px 2px 0 #B3B3B3
    padding 1em

    .brand
      font-family 'Sacramento', cursive
</style>



