import Vue from 'vue'
import Vuex from 'vuex'
import ls from 'store'

Vue.use(Vuex)

const themes = ['', 'theme-dark']

const store = new Vuex.Store({
  state: {
    theme: ls.get('z0ey.config.theme') || ''
  },

  getters: {
    theme: state => state.theme
  },

  mutations: {
    ToggleTheme (state, { name = '' }) {
      const idx = themes.findIndex(ele => ele === state.theme)
      state.theme = themes[(idx + 1) % themes.length]
      ls.set('z0ey.config.theme', state.theme)
    }
  }
})

export default store



