<template>
  <div v-bind:class="['header', theme]">
    <div class="logo" @click="logoClick">z0ey</div>
    <div class="menu">
      <div class="button" @click="toggleTheme">
        <i class="fa fa-moon"></i>
      </div>
      <div class="button mobile-disabled" @click="toggleFullscreen">
        <i class="fa fa-expand"></i>
      </div>
      <div class="button mobile-disabled" @click="addPane">
        <i class="fa fa-plus"></i>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import Events from '@/events'

export default {
  name: 'header-bar',

  computed: {
    ...mapGetters([
      'theme'
    ])
  },

  methods: {
    logoClick () {
      Events.$emit('logoClick')
    },

    toggleTheme () {
      this.$store.commit('ToggleTheme', {})
    },

    toggleFullscreen () {
      Events.$emit('fullscreen')
    },

    addPane () {
      Events.$emit('vertical-split-window')
    }
  }
}
</script>

<style lang="stylus">
@import '~@/theme.styl'

.header
  width 100%
  height 50px
  position fixed
  display flex
  flex 1
  justify-content center
  background-color $theme-default-bg
  border-bottom 1px solid $theme-default-border

  &.theme-dark
    border-bottom 1px solid $theme-dark-border

  &.theme-dark
    background-color $theme-dark-bg

  .logo
    cursor pointer
    font-family 'Sacramento', 'Damion', cursive
    font-size 2em
    color $theme-nonactive
    transition $theme-hover-transition

    &:hover
      color inherit
      text-shadow 1px 1px

  .menu
    user-select none
    line-height 50px
    height 50px
    position absolute
    right 0
    top 0
    cursor pointer

    .button
      margin-right 1em
      display inline-block
      color $theme-nonactive
      transition $theme-hover-transition

      &:hover
        color inherit

      &.mobile-disabled
        @media only screen and (max-width: 767px)
          display none
</style>



