import store from 'store'

const KEY_CONFIG_POSITIONS = 'z0ey.config.positions'

export default {
  loadAllDrafts () {
    const positions = store.get(KEY_CONFIG_POSITIONS)
    if (!positions) {
      return []
    }
    let allDrafts = []
    for (const uuid of positions) {
      const note = store.get(`z0ey.draft.${uuid}`)
      allDrafts.push({
        uuid,
        note: note
      })
    }
    return allDrafts
  },

  saveAllDrafts (drafts) {
    let positions = []
    drafts.map((draft) => {
      store.set(`z0ey.draft.${draft.uuid}`, draft.note)
      positions.push(draft.uuid)
    })
    store.set(KEY_CONFIG_POSITIONS, positions)
  },

  removeDraft (uuid) {
    store.remove(`z0ey.draft.${uuid}`)
    const positions = store.get(KEY_CONFIG_POSITIONS)
    store.set(KEY_CONFIG_POSITIONS, positions.filter((_uuid) => uuid !== _uuid))
  }
}



