import Vue from 'vue'
import Mousetrap from 'mousetrap'

const Events = new Vue()

Mousetrap.bind([': v s', '+'], () => {
  Events.$emit('vertical-split-window')
})

Mousetrap.bind('f', () => {
  Events.$emit('fullscreen')
})

Mousetrap.bind('esc', () => {
  Events.$emit('keyup-esc')
})

export default Events



